import React, { useState } from 'react';
import Button from 'react-bootstrap/Button';
import Container from 'react-bootstrap/Container';
import { confirmAlert } from 'react-confirm-alert';
import 'react-confirm-alert/src/react-confirm-alert.css'
import HookuseStateCss from './HookuseState.module.css';

const ButtonCounter = (props) => {
    return (
        <React.Fragment>
            <br /> <br />
            <Button variant={props.variant} onClick={props.CounterType} disabled={props.disabled} className={HookuseStateCss.CounterButtonValue}> {props.buttonValue}</Button>
        </React.Fragment>
    )
}

const HookuseState = () => {
    const [counterObj, SetCounterValue] = useState({ counter: 0, counterBtnColor: 'cadetblue' });
    const [errorMessage, ErrorMessageDisplay] = useState(null);

    const increaseCounter = () => {
        const incObject = { ...counterObj, counter: counterObj.counter + 1, counterBtnColor: 'green' }
        SetCounterValue(incObject)
        ErrorMessageDisplay(errorMessage => null)
    }

    const decreaseCounter = () => {
        if (counterObj.counter > 0) {
            const dcrObject = { ...counterObj, counter: counterObj.counter - 1, counterBtnColor: 'red' }
            SetCounterValue(dcrObject)
            ErrorMessageDisplay(errorMessage => null)
        } else {
            const dcrObject = { ...counterObj, counter: 0 }
            SetCounterValue(dcrObject)
            ErrorMessageDisplay(errorMessage => "Counter has already been reset")
            setTimeout(() => {
                ErrorMessageDisplay(errorMessage => null)
            }, 5000);
        }
    }

    const resetCounter = () => {
        let rstObjectReset = { ...counterObj, counter: 0, counterBtnColor: 'cadetblue' }
        let rstObjectExisting = { ...counterObj, counter: counterObj.counter }
        confirmAlert({
            title: 'Reset Counter',
            message: 'Are you sure ?',
            buttons: [
                {
                    label: 'Reset',
                    onClick: () => {
                        SetCounterValue(rstObjectReset);
                        ErrorMessageDisplay(errorMessage => null)
                    }
                },
                {
                    label: 'Existing Value',
                    onClick: () => {
                        SetCounterValue(rstObjectExisting)
                    }
                }
            ]
        })
    };

    return (
        <React.Fragment>
            <Container>
                <div className={HookuseStateCss.CounterDiv}>
                    <h5>Day-8 React Hook - useState()</h5>
                    <br />
                    <h5 style={{ color: counterObj.counterBtnColor}} >Counter : {counterObj.counter}</h5>
                    <label className={HookuseStateCss.ErrorMsg}>{errorMessage}</label>
                    <ButtonCounter variant="outline-success" CounterType={() => increaseCounter()} buttonValue="Increment Counter" />
                    <ButtonCounter variant="outline-danger" CounterType={() => decreaseCounter()} buttonValue="Decrement Counter" />
                    <ButtonCounter variant="outline-secondary" CounterType={() => resetCounter()} buttonValue="Reset Counter" disabled={counterObj.counter === 0} />
                </div>
            </Container>
        </React.Fragment >
    );
};

export default HookuseState;